import { toJSArray, toJSString, sharedFontManager, weightOfFont, traitsOfFont } from "nsHelpers.js";
import sketch from 'sketch'

export function backgroundColor(layer) {
  if (layer.hasBackgroundColor && layer.hasBackgroundColor()) {
    return "#" + layer.backgroundColor().immutableModelObject().hexValue();
  } else {
    return null;
  }
}

export function layers(layer) {
  if (layer.layers) {
    return toJSArray(layer.layers());
  } else {
    return [];
  }
}

export function stringValue(layer) {
  if (layer.className() == "MSTextLayer") {
    return toJSString(layer.stringValue());
  } else {
    return null;
  }
}

function fontStyle(fontManager, font) {
  let mask = traitsOfFont(fontManager, font);
  let italicMask = 1;
  if ((mask & italicMask) == italicMask) {
    return "italic";
  } else {
    return "normal";
  }
}

/**
 * The weightOfFont returns an approximation of the weight of the given font.
 *
 * The font weight array is constructed from Robin's comment:
 * https://sketchplugins.com/d/193-how-to-get-font-weight/4
 */
function fontWeight(fontManager, font) {
  let fws = [100, 100, 100, 200, 300, 400, 500, 500, 600, 700, 800, 900, 900, 900, 900, 900];
  let idx = weightOfFont(fontManager, font);
  return fws[idx];
}

export function fontMetrics(layer) {
  if (layer.className() == "MSTextLayer") {
    let fontManager = sharedFontManager();
    let font = layer.font();
    let result = {
      fontFamily: toJSString(font.familyName()),
      fontWeight: fontWeight(fontManager, font),
      fontStyle: fontStyle(fontManager, font)
    };
    return result;
  } else {
    return null;
  }
}

export function lineHeight(layer) {
  let className = String(layer.className());
  if (className == "MSTextLayer" || className == "MSImmutableTextLayer") {
    let l = sketch.fromNative(layer);
    let lh = l.style.lineHeight;
    if (lh) {
      return String(lh) + "px";
    } else {
      // to retrieve the actual pixel value Sketch uses when the user hasn't
      // picked any particular line-height, we can use
      // l.style.getDefaultLineHeight(). However, that'll lead to a large
      // variation in possible line-heights, and instead we can rely on CSS's
      // "normal" property for line-height, in which the browser, similar to
      // Sketch's default line height behaviour, picks an appropriate line-height
      // for the given font-family.
      return "normal";
    }
  } else {
    return null;
  }
}

export function cornerRadiusString(layer) {
  if ((layer.className() == "MSRectangleShape")) {
    // Errant Sketch designs sometime has a "," inside their borderRadius
    // (eg: Invision Velocity dashboard --
    // https://www.sketchappsources.com/resource/download-3793.html -- layer 
    // Element/Control/Toggle inactive > Container > Base)
    return toJSString(layer.cornerRadiusString().replace(/,/g, ""))
  }
  else { return null }
}

export function isFileSaved(doc) {
  return (!(doc.fileURL() == null))
}

export function filepath(doc) {
  return toJSString(doc.fileURL().path());
}

export function filename(doc) {
  return toJSString(doc.fileURL().lastPathComponent());
}

export function newMutableCounterpart(layer) {
  return layer.newMutableCounterpart();
}

export function modifiedMasterByApplyingInstance(symbolInstance) {
  let documentData = context.document.documentData();
  let symbolID = symbolInstance.symbolID();
  let immutableInstance = symbolInstance.immutableModelObject();
  let immutableMaster = documentData.symbolWithID(symbolID).immutableModelObject();

  return immutableMaster.modifiedMasterByApplyingInstance_inDocument_(immutableInstance, nil);
}

export function sketchtoolPath() {
  return toJSString(NSBundle.mainBundle().pathForResource_ofType_inDirectory("sketchtool", nil, "sketchtool/bin"));
}

export function zoomToSelection(doc) {
  doc.eventHandlerManager().currentHandler().zoomToSelection()
}

export function clearSelections(doc) {
  // https://github.com/BohemianCoding/SketchAPI/blob/13541c0246a585695bd6e7b0c3cd8899a514b377/Source/dom/models/Selection.js#L63
  doc.currentPage().changeSelectionBySelectingLayers(null);
}