import BrowserWindow from 'sketch-module-web-view'

function makeBrowserWindow(uniqueAppId) {
  const sketchFrame = NSApplication.sharedApplication().mainWindow().frame();
  const options = {
    identifier: uniqueAppId,
    width: 700,
    height: 310,
    show: false,
    alwaysOnTop: true,
    movable: true,
    fullscreenable: false,
    frame: true,
  };
  // Show the window at the bottom center of Sketch
  options.x = (sketchFrame.origin.x) + (sketchFrame.size.width - options.width) / 2;
  // yeah some magic whatever
  options.y = (sketchFrame.origin.y + sketchFrame.size.height) - (options.height - 12);

  let browserWindow = new BrowserWindow(options);
  browserWindow.setMovable(true);

  return browserWindow
}


/* 
Written in JS because couldn't figure out how to clear the interval from within setInterval in Reason. Can't make `intervalId` a ref because its type is  abstract in Js.Global and so no way to initialize with an empty value 
*/
function setIntervalWithMaxcount__(interval, callback, maxAttempts, onTimeout) {
  let count = 0;
  let intervalId = setInterval(() => {
    if (count < maxAttempts) {
      count = count + 1;
      callback()
    } else {
      clearInterval(intervalId);
      onTimeout();
    }
  }, interval);
  return intervalId;
};

// construct an error object with a 
// program-supplied high-level error message,
// and an errorObj
function error(message, errorObj) {
  let extra
  if (errorObj == null)
    extra = ""
  else if (typeof errorObj == "undefined")
    extra = ""
  else if (errorObj.message)
    // standard Javascript error object
    extra = errorObj.message
  else if (errorObj.error)
    // error hash returned by server. 422 as in:
    // (https://gist.github.com/rodacato/b29db010dfcd0f38aadd44aceb95213e#create-a-new-design-import-resource)
    extra = errorObj.error
  else if (errorObj.localizedDescription)
    // objective-c error object
    extra = String(errorObj.localizedDescription())
  else if (typeof errorObj == "string")
    // custom string error generated by our own code
    extra = errorObj
  else if (typeof errorObj == "object")
    // a Javascript error object - pretty print it
    extra = JSON.stringify(errorObj, null, 2)
  else
    extra = String(errorObj)

  let h = [
    message,
    extra
  ]

  log(errorObj)
  log(h)

  return h
}


export { makeBrowserWindow, setIntervalWithMaxcount__, error }