const fs = require("@skpm/fs");

/*
  Takes a NSArray - https://developer.apple.com/documentation/foundation/nsarray?changes=_4
  and returns a javascript array.

  TODO: Use the better implementation from
  https://github.com/BohemianCoding/SketchAPI/blob/develop/Source/dom/utils.js#L12
*/

export function toJSArray(collection) {
  let element;
  let result = [];
  let enumerator = collection.objectEnumerator();
  while (element = enumerator.nextObject()) {
    result.push(element);
  }
  return result;
}

export function toJSString(nsString) {
  return nsString + "";
}

export function runShellCommand(binpath, args) {
  let stdoutPipe = NSPipe.pipe();
  let stderrPipe = NSPipe.pipe();
  let task = NSTask.alloc().init();
  task.setLaunchPath(binpath);
  task.setArguments(args);
  task.setStandardOutput(stdoutPipe);
  task.setStandardError(stderrPipe);
  task.launch();
  task.waitUntilExit();
  let stdoutData = stdoutPipe.fileHandleForReading().readDataToEndOfFile();
  let stdoutString = NSString.alloc().initWithData_encoding(stdoutData, NSUTF8StringEncoding);
  let stderrData = stderrPipe.fileHandleForReading().readDataToEndOfFile();
  let stderrString = NSString.alloc().initWithData_encoding(stderrData, NSUTF8StringEncoding);
  return [toJSString(stdoutString), toJSString(stderrString)];
}

export function temporaryDirectory() {
  let name = NSUUID.UUID().UUIDString().stringByAppendingPathExtension("io.protoship.codegen");
  let temporaryDirectory = NSTemporaryDirectory();
  let path = temporaryDirectory.stringByAppendingPathComponent(name);
  return toJSString(path);
}

export function fileSize(filePath) {
  let fileManager = NSFileManager.defaultManager();
  let attributes = fileManager.attributesOfItemAtPath_error(filePath, nil);

  if (attributes) {
    return attributes.fileSize();
  } else {
    return 0;
  }
}

// Find checksum compatible with ActiveStorage
// activestorage-5.2.0/lib/active_storage/service/disk_service.rb#ensure_integrity_of
export function md5Base64(filepath) {
  let script = "openssl dgst -md5 -binary '" + filepath + "' | openssl enc -base64";
  let result = runShellCommand("/bin/bash", ["-c", script])
  return toJSString(result[0].trim());
}

export function readBinaryFile(filepath) {
  return NSData.alloc().initWithContentsOfFile(filepath);
  // return fs.readFileSync(filepath, {encoding: "binary"})
}

export function openURL(url) {
  let nsUrl = NSURL.URLWithString(String(url));
  if (!NSWorkspace.sharedWorkspace().openURL(nsUrl)) {
    log("Could not open url:" + nsUrl.description())
  }
}

export function sharedFontManager() {
  return NSFontManager.sharedFontManager();
}

export function weightOfFont(fontManager, font) {
  return fontManager.weightOfFont(font);
}

export function traitsOfFont(fontManager, font) {
  return fontManager.traitsOfFont(font);
}